import React, { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import AssignmentIndOutlined from '@material-ui/icons/AssignmentIndOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';
import InputMask from 'react-input-mask';

import apiValida from '../services/apiValida';
import apiEnvia from '../services/apiEnvia';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      Devops/Developer{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function FormularioCPF({history}) {
  const [cpf, setCpf] = useState('');
  const [resposta, setResposta] = useState('');  
  const [submitForm, setSubmitForm] = useState(false);  
  const classes = useStyles();

  async function handleValidar(e) {
    e.preventDefault();

    setSubmitForm(true);        

    const responseValida = await apiValida.post('/validar', {
      cpf,
    });

    setResposta(responseValida.data);        
  }

  async function handleSubmit(e) {
    e.preventDefault();

    setSubmitForm(true);        
    
    const responseValida = await apiValida.post('/validar', {
      cpf,
    });

    setResposta(responseValida.data);    

    console.log(cpf+' - '+resposta);

    if(resposta){
      await apiEnvia.post('/enviar', {
        cpf,
      });            

      history.push('/main');       
    }    
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <AssignmentIndOutlined />
        </Avatar>
        <Typography component="h1" variant="h5">
          Registro de CPF TESTE
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>              
              <InputMask mask="999.999.999-99" value={cpf} onChange={e => setCpf(e.target.value)}>
                {() => <TextField
                variant="outlined"
                required
                fullWidth
                id="cpf"
                label="CPF Cliente"
                name="cpf"
                autoComplete="cpf"                
              />}
              </InputMask>              
            </Grid>
            {resposta && (
              <Grid item>
                <Button variant="contained" color="primary" type="submit">
                  Enviar
                </Button>
              </Grid>              
            )}            
            {!resposta && (
              <Grid item>
              <Button variant="contained" color="secondary" type="submit" onClick={handleValidar}>
                Validar
              </Button>
            </Grid>                        
            )}            
          </Grid>            
        </form>        
      </div>
      {(!resposta && submitForm) && (
        <Box mt={5}>
          <Alert variant="filled" severity="error">
            CPF de Cliente Inválido!
          </Alert>
        </Box>
      )}      
      {resposta && (
        <Box mt={5}>
          <Alert variant="filled" severity="success">
            CPF {cpf} Válido!
          </Alert>
        </Box>
      )}      
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
  );
}
